.users{
    width: 100%;
    height: 100vh;
    overflow-y: auto;

    .switch {
        .switch {
            position: relative;
            display: inline-block;
            width: 50px;
            height: 24px;
            margin-top: 14px;
          }
          
          .switch input { 
            opacity: 0;
            width: 0;
            height: 0;
          }
          
          .slider {
            position: absolute;
            cursor: pointer;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #CFCFD0;
            -webkit-transition: .4s;
            transition: .4s;
          }
          
          .slider:before {
            position: absolute;
            content: "";
            height: 16px;
            width: 16px;
            left: 4px;
            bottom: 4px;
            background-color: white;
            -webkit-transition: .4s;
            transition: .4s;
          }
          
          input:checked + .slider {
            background: #901CEE
            ;
          }
          
          input:focus + .slider {
            box-shadow: 0 0 1px #901CEE ;
          }
          
          input:checked + .slider:before {
            -webkit-transform: translateX(26px);
            -ms-transform: translateX(26px);
            transform: translateX(26px);
          }
          
          /* Rounded sliders */
          .slider.round {
            border-radius: 34px;
          }
          
          .slider.round:before {
            border-radius: 50%;
          }
    }
}

.user-details {
    .upper-detail-page{
      
        padding: 45px 20px;
        .button-red{
            outline: none;
            border: none;
            box-shadow: none;
            padding: 13px 30px;
            background: linear-gradient(180deg, #E72D1D 0%, #E71D36 100%);
