.btn-common {
  background: #901cee;
  box-shadow: 0px 10px 14px rgba(242, 177, 15, 0.3);
  border-radius: 8px;
  margin: 10px 0;
  padding: 12px 30px;
  color: #fff;
  text-decoration: none;
  border: none;
  // font-size: 13px;
  width: 100%;

  &:hover {
    color: #fff;
    //  box-shadow: 0px 10px 14px rgba(33, 42, 56, 0.3);
    //  background-color: linear-gradient(180deg, #133572 0%, #3568C1 100%);;
  }
  &:active {
    color: #fff !important;
    //  background-color: linear-gradient(180deg, #133572 0%, #3568C1 100%);;
  }
  &:focus {
    outline: none;
  }
}

.ptb {
  padding: 60px 0;
}

.btn-red {
  background: #f15b46;
  border: 1px solid #be3817;
  box-sizing: border-box;
  padding: 12px 30px;
  border-radius: 8px;
  color: #fff !important;
  text-decoration: none;
  // font-size: 13px;
  // width: 100%;
}

.btn-red:hover {
  opacity: 0.75;
}
